import React, { Component } from "react";
import Header from "../header";
import AddForm from "../addForm";
import ListItem from "../listItem/index";

import "./app.css";

export default class App extends Component {
  itemId = 10;

  state = {
    data: [
      { name: "Купити хліб", importand: false, check: false, id: 1 },
      { name: "Заправитись", importand: false, check: false, id: 2 },
      { name: "Обід на 14:00", importand: false, check: false, id: 4 },
    ],
  };
  onDelete = (id) => {
    this.setState(({ data }) => {
      const index = data.findIndex((item) => item.id === id);
      const bef = data.slice(0, index);
      const aft = data.slice(index + 1);

      const end = [...bef, ...aft];
      return {
        data: end,
      };
    });
  };

  addForm = (text) => {
    const newData = {
      name: text,
      importand: false,
      id: this.itemId++,
    };
    this.setState(({ data }) => {
      if (newData.name === "") {
        alert("Ведіть нотатку");
        return;
      }

      const newData2 = [...data, newData];

      return { data: newData2 };
    });
  };

  // calcult = (arr, id, items) => {
  //   console.log(items);
  //   const index = arr.findIndex((item) => item.id === id);

  //   const oldItem = arr[index];
  //   const newItem = { ...oldItem, items: !items };

  //   return {
  //     data: [...arr.slice(0, index), newItem, ...arr.slice(index + 1)],
  //   };
  // };
  onDone = (id) => {
    this.setState(({ data }) => {
      const index = data.findIndex((item) => item.id === id);

      const oldItem = data[index];
      const newItem = { ...oldItem, check: !oldItem.check };
      const checkDone = [
        ...data.slice(0, index),
        newItem,
        ...data.slice(index + 1),
      ];
      return {
        data: checkDone,
      };
    });
  };
  onImportand = (id) => {
  
    this.setState(({ data }) => {
      const index = data.findIndex((item) => item.id === id);

      const oldItem = data[index];
      const newItem = { ...oldItem, importand: !oldItem.importand };
      const checkDone = [
        ...data.slice(0, index),
        newItem,
        ...data.slice(index + 1),
      ];
      return {
        data: checkDone,
      };
    });
  };

  render() {
    const { data } = this.state;

    return (
      <>
        <Header data={data} />
        <ListItem
          onImportandItem={this.onImportand}
          onDoneItem={this.onDone}
          del={this.onDelete}
          data={data}
        />
        <AddForm addItem={this.addForm} />
      </>
    );
  }
}
